<template>
  <div class="wrap">
    <div class="list">
      <div class="box">
        <div class="item" v-for="(item, index) in list" @click="addStyle(index)" :key="index">
          <div class="content">{{item.name}}</div>
          <div class="img" v-if="index1 === index"></div>
        </div>
      </div>
    </div>
    <div class="makeSure" @click="goTo">确定</div>
  </div>
</template>

<script>
  export default {
    name: "prizeList",
    data() {
      return {
        list: [
          {
            name: '1'
          },
          {
            name: '2'
          },
          {
            name: '3'
          },
          {
            name: '4'
          },
          {
            name: '5'
          },
          {
            name: '6'
          },
          {
            name: '7'
          },
          {
            name: '8'
          }

        ],
        index1: '',
        hasLogin: false
      }
    },
    methods: {
      // 选择礼物
      addStyle (index) {
        console.log(index)
        this.index1 = index
      },
      goTo () {
        if (this.hasLogin === true) {
          this.$router.push('/chargeActivity/collectPrize')
        } else {
          window.location.href = 'https://apps.cfae.cn/web/share/login.html'
        }
      }
    }
  }
</script>

<style scoped>
  .wrap {
    height: 100vh;
    padding: 20px;
    box-sizing: border-box;
  }
  .list {
    overflow-y: hidden;
  }
  .box {
    width: 600px;
    overflow-x: auto;
  }
  .item {
    width: 100px;
    height: 100px;
    background-color: red;
    float: left;
    border: 1px solid #000000;
    box-sizing: border-box;
  }
  .img {
    width: 20px;
    height: 20px;
    background: pink;
    /*display: none;*/
  }
  .makeSure {
    margin-top: 20px;
    text-align: center;
  }
</style>



